import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';

// material
import { Skeleton, Grid, Paper, Stack, Typography, Box, Tooltip, Link } from '@material-ui/core';

// ----------------------------------------------------------------------

BookingItem.propTypes = {
  product: PropTypes.object
};

function BookingItem({ product }) {
  const { name, cover } = product;
  return (
    <Paper sx={{ borderRadius: 2, bgcolor: 'background.neutral' }}>
      <Box sx={{ p: 1, position: 'relative' }}>
        <Box
          component="img"
          src={cover}
          sx={{ borderRadius: 1.5, top: 0, width: '100%', height: '200px', objectFit: 'cover' }}
        />
      </Box>

      <Stack spacing={1} sx={{ p: 2, pt: 1, pb: 1 }}>
        <Stack direction="row" alignItems="center" justifyContent="space-between">
          <Link color="inherit" underline="none">
            <Typography variant="subtitle2" noWrap>
              {name}
            </Typography>
          </Link>
          <Stack spacing={0}>
            <Stack direction="row" alignItems="center" spacing={1}>
              <Icon icon="logos:ethereum" width={16} height={16} />
              <Typography variant="subtitle2" noWrap>
                0.01
              </Typography>
            </Stack>
          </Stack>
        </Stack>

        <Stack direction="row" justifyContent="space-between">
          <Tooltip title="NFT Contract">
            <Link href="#" underline="none" target="_blank" rel="noopener">
              <Stack direction="row" alignItems="center" spacing={0.5}>
                <Icon icon="teenyicons:contract-outline" width={16} height={16} />
                <Typography variant="body2" noWrap>
                  0xE53821
                </Typography>
              </Stack>
            </Link>
          </Tooltip>
          <Tooltip title="NFT ID">
            <Link href="#" underline="none" target="_blank" rel="noopener">
              <Stack direction="row" alignItems="center" spacing={0.5}>
                <Icon icon="ant-design:number-outlined" width={16} height={16} />
                <Typography variant="body2" noWrap>
                  142
                </Typography>
              </Stack>
            </Link>
          </Tooltip>
          <Tooltip title="Author Address">
            <Link href="#" underline="none" target="_blank" rel="noopener">
              <Stack direction="row" alignItems="center" spacing={0.5}>
                <Icon icon="bi:shield-check" width={16} height={16} />
                <Typography variant="body2" noWrap>
                  0x1432
                </Typography>
              </Stack>
            </Link>
          </Tooltip>
        </Stack>
      </Stack>
    </Paper>
  );
}

const SkeletonLoad = (
  <>
    {[...Array(12)].map((_, index) => (
      <Grid item xs={12} sm={6} md={3} key={index}>
        <Skeleton variant="rectangular" width="100%" sx={{ paddingTop: '115%', borderRadius: 2 }} />
      </Grid>
    ))}
  </>
);

ProductList.propTypes = {
  products: PropTypes.array.isRequired,
  isLoad: PropTypes.bool
};

export default function ProductList({ products, isLoad, ...other }) {
  return (
    <Grid container spacing={3} {...other}>
      {products.map((product) => (
        <Grid key={product.id} item xs={12} sm={6} md={4}>
          <BookingItem product={product} />
        </Grid>
      ))}

      {isLoad && SkeletonLoad}
    </Grid>
  );
}
